import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import { AuthContextProvider } from "./context/AuthContext"; // to provide the authentication.
import { ChatContextProvider } from "./context/ChatContext";
import { ToastProvider } from 'react-toast-notifications';
 // context api to provide the states directly to the components which have their needs.

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <AuthContextProvider> 
      <ChatContextProvider> 
        <React.StrictMode> 
          <ToastProvider autoDismiss autoDismissTimeout={5000} placement="top-left">
          <App />
          </ToastProvider> 
        </React.StrictMode>
      </ChatContextProvider> 
  </AuthContextProvider>
);
